import React, { useEffect, useState } from "react";
import { joinTournamentAPI, deleteTeam } from "../../../APIS/apis";
import { Button, LinearProgress } from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import { S3_URL } from "../../../APIS/apis";
import PreviewIcon from "@mui/icons-material/Preview";
import DeleteIcon from "@mui/icons-material/Delete";
import deleteClickedTeam from "../common/deleteClickedTeam";
import joinTournament from "../common/joinTournament";
// import joinTournament from "../../Tournaments/common/joinTournament";
import selectTeam from "../common/selectTeam";
import "../style/index.css";
import {
  chooseTeamClose,
  chooseTeamOpen,
} from "../common/chooseTeamAnimations";
import { removeCoinsFromLocalStorage } from "../../../CommonFunctions/functions";
import {getLeaderboardAsync} from "../../../Redux/LeaderBoard/LeaderBoardSlice";
import {getTournamentByIdAsync} from "../../../Redux/LeaderBoard/LeaderBoardSlice";
import {useDispatch} from "react-redux";
import {getTransactionsAsync} from "../../../Redux/Transaction/TransactionSlice";
import {getTournamentAsync} from "../../../Redux/Tournaments/TournamentSlice";
import CommonModal from "../../../Common/Modal/Modal";
export default function JoinTournamentDrawer({
  teams,
  tournamentId,
  setErrorMessage,
  setErrorMessageSnackOpen,
  navigate,
  tournaments,
  intervalId = null,
}) {
  const dispatch = useDispatch();
  useEffect(() => {
    document
      .getElementsByClassName("overlay-div")[0]
      .addEventListener("mouseup", function (event) {
        var pol = document.getElementById("choose-team-div");
        
        if (!pol.contains(event.target)) {
          chooseTeamClose();
          return;
        }
      });
  }, []);

  const confirmationModal = () => {
    return(
      <CommonModal>

      </CommonModal>
    )
  }

  async function featchtournament(tournamentId){
    setTimeout(async () => {
      await dispatch(getTournamentByIdAsync(tournamentId));
    }, 4500);
  }
  return (
    <div key={"enter-tournament"} id="choose-team-div" className="display-none">
      <div className="choose-team-bar"></div>
      <div id="all-teams-info">
        <div>
          <span
            className="font-size-25 font-weight-700 mb-10"
            style={{ color: "var(--black)" }}
          >
            Choose Team
          </span>
          <br />
          <span
            className="font-size-15 font-weight-500"
            style={{ color: "var(--grey-shade)" }}
          >
            {teams && teams.length === 0 ? (
              <>Create a team and start winning.</>
            ) : (
              <>
                Select a team that you think should represent you in this
                contest.
              </>
            )}
          </span>
        </div>
        <div className="all-teams">
          <div style={{ padding: "0" }}>
            {teams.map((team, index) => {
              var clickedId = "team-" + index;
              return (
                <div
                  id={"team-" + index}
                  className="mb-15"
                  style={{ padding: "0", borderRadius: "12px" }}
                  onClick={() => selectTeam(clickedId, teams)}
                >
                  <div className="team">
                    <span
                      className="font-size-18 font-weight-600"
                      style={{ color: "var(--grey-shade)" }}
                    >
                      {team.name}
                    </span>
                    <span id="visible-coins" style={{ marginLeft: "auto" }}>
                      {/* <CheckCircleIcon
                        className="select-team-button team-buttons"
                        // onClick={() => selectTeam(clickedId, teams)}
                        fontSize="large"
                      /> */}
                      <PreviewIcon
                        className="preview-team-button team-buttons ml-5"
                        fontSize="large"
                        onClick={(event) => {
                          event.cancelBubble = true;
                          if(event.stopPropagation) event.stopPropagation();
                          // document.querySelector(
                          //   "#" + clickedId + " .select-team-button"
                          // ).style.display = "none";
                          var allTeams =
                            document.getElementsByClassName("team");
                          for (var i = 0; i < allTeams.length; i++) {
                            if (clickedId !== "team-" + i)
                              allTeams[i].parentNode.classList.add("display-none");
                          }
                          document
                            .getElementById(clickedId)
                            .classList.remove("display-none");
                          document
                            .getElementById(
                              "team-coins-" + clickedId.split("-")[1]
                            )
                            .classList.remove("display-none");
                          document.getElementById(
                            "back-from-teamview-button"
                          ).style.display = "block";
                        }}
                      />
                      <DeleteIcon
                        className="delete-team-button team-buttons ml-5"
                        onClick={(event) =>{
                          event.cancelBubble = true;
                          if(event.stopPropagation) event.stopPropagation();
                          deleteClickedTeam(event, teams, deleteTeam)}
                        }
                        fontSize="large"
                      />
                    </span>
                  </div>
                  <div
                    id={"team-coins-" + index}
                    className="display-none team-coins"
                  >
                    {team.selectedCoins.map((coin, index) => {
                      let selected_coin_css =
                        coin.category === "Superstar"
                          ? "team-view-coin-card-superstar"
                          : coin.category === "Mooning"
                          ? "team-view-coin-card-mooning"
                          : "team-view-coin-card-rekt";
                      return (
                        <div
                          className={
                            "teamview-coin-card mr-10 " + selected_coin_css
                          }
                        >
                          {coin.rank !== -1 && (
                            <span className="ribbon-team-view">
                              <span className="ribbon2-team-view">
                                {coin.rank}
                              </span>
                            </span>
                          )}
                          <img
                            src={S3_URL + coin.symbol + ".png"}
                            width="40"
                            height="40"
                          />
                          <span className="font-size-12 font-weight-500">
                            {coin.name}
                          </span>
                          <span className="font-size-12 font-weight-500">
                            {coin.category === "Defi" ? "Rekt" : coin.category}
                          </span>
                        </div>
                      );
                    })}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        <div className="mt-10" id="create-new-button-div-drawer">
          <Button
            variant="filled"
            id="jointournament-button"
            style={{
              backgroundColor: "var(--golden)",
              fontWeight: "600",
              fontSize: "17px",
            }}
            onClick={async () => {
              await joinTournament(
                teams,
                tournamentId,
                joinTournamentAPI,
                setErrorMessage,
                setErrorMessageSnackOpen,
                tournaments
              );
              
        
              dispatch(getTournamentAsync());
              dispatch(getLeaderboardAsync(tournamentId));
              dispatch(getTransactionsAsync());
            
              await dispatch(getTournamentByIdAsync(tournamentId));
              await featchtournament(tournamentId);
            }}
          >
            Join Now
          </Button>
          <Button
            variant="filled"
            id="back-from-teamview-button"
            style={{
              backgroundColor: "var(--golden)",
              fontWeight: "600",
              fontSize: "17px",
            }}
            onClick={(event) => {
              var allSelectButtons =
                document.getElementsByClassName("select-team-button");
              var allTeams = document.getElementsByClassName("team");
              for (var i = 0; i < allSelectButtons.length; i++) {
                allSelectButtons[i].style.display = "inline-block";
              }
              for (var i = 0; i < allTeams.length; i++) {
                allTeams[i].parentNode.classList.remove("display-none");
                document
                  .getElementById("team-coins-" + i)
                  .classList.add("display-none");
              }
              event.target.style.display = "none";
            }}
          >
            Back
          </Button>
          {teams.length === 0 ? (
            <Button
              variant="contained"
              id="new-team-type1"
              style={{}}
              onClick={() => {
                if (intervalId) clearInterval(intervalId);
                removeCoinsFromLocalStorage();
                navigate("/teams/createteam", {
                  state: {
                    comingFrom: window.location.pathname,
                    tournamentId: tournamentId,
                  },
                });
              }}
            >
              Create New Team
            </Button>
          ) : (
            <Button
              id="new-team-type2"
              style={{
                color: "var(--golden)",
                fontWeight: "600",
                fontSize: "15px",
                textTransform: "capitalize",
              }}
              onClick={() => {
                if (intervalId) clearInterval(intervalId);
                removeCoinsFromLocalStorage();
                navigate("/teams/createteam", {
                  state: {
                    comingFrom: window.location.pathname,
                    tournamentId: tournamentId,
                  },
                });
              }}
            >
              Create New Team
            </Button>
          )}
        </div>
      </div>
      {/*{confirmationModal()}*/}
    </div>
  );
}
